<template>
    <li class="sort-card" @click="routeToSortDetail(item.name, target)">
        <div class="content">
            <span class="sort-title">{{ item.name }}</span>
            <span class="sort-count">{{ item.bookCount }}本</span>
        </div>
        <div class="sort-icon">
            <img
                v-for="(img, index) in item.bookCover"
                :key="index"
                :src="'https://statics.zhuishushenqi.com' + img"
            />
        </div>
    </li>
</template>

<script>
export default {
    name: "SortCard",
    props: {
        item: Object,
        target: String,
    },
    methods: {
        routeToSortDetail(name, target) {
            this.$router.push({
                name: "SortDetail",
                params: { major: name, target: target },
            });
        },
    },
};
</script>

<style lang="less" scoped>
.sort-card {
    height: 65px;
    background-color: #f9f9fb;
    margin-left: 10px;
    margin-top: 15px;
    display: flex;
    flex-direction: row;
    .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: left;
        color: #868589;
        margin: 10px;
        .sort-title {
            font-size: 14px;
        }
        .sort-count {
            font-size: 12px;
        }
    }

    .sort-icon {
        width: 45px;
        margin-top: 10px;
        margin-right: 15px;
        position: relative;
        left: 0;
        top: 0;
        img {
            width: 60%;
            vertical-align: middle;
            position: absolute;
        }
        img:nth-child(1) {
            left: 0;
            bottom: 0;
            z-index: 1;
        }
        img:nth-child(2) {
            right: -10px;
            bottom: 0;
            z-index: 1;
        }
        img:nth-child(3) {
            width: 80%;
            left: 18%;
            bottom: 0;
            z-index: 2;
        }
    }
}
</style>
