<template>
    <div>
        <NavHead></NavHead>
        <div class="sort">
            <ul class="sort-list">
                <li
                    v-for="(item, index) in sortList"
                    :key="index"
                    :class="{ active: item.active }"
                    @click="changeItem(index)"
                >
                    <span>{{ item.text }}</span>
                </li>
            </ul>
            <ul class="sort-content">
                <SortCard
                    v-for="(item, index) in changeSort()"
                    :key="index"
                    :item="item"
                    :target="currentTarget()"
                ></SortCard>
            </ul>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import types from "@/store/types.js";
import NavHead from "@/components/NavHead.vue";
import SortCard from "@/components/SortCard.vue";
export default {
    name: "BookSort",
    data() {
        return {
            sortList: [
                {
                    text: "男生",
                    active: true,
                    target: "male",
                },
                {
                    text: "女生",
                    active: false,
                    target: "female",
                },
                {
                    text: "出版",
                    active: false,
                    target: "press",
                },
                {
                    text: "标签",
                    active: false,
                    target: "picture",
                },
            ],
        };
    },
    components: {
        NavHead,
        SortCard,
    },
    computed: {
        ...mapState({
            male: (state) => state.sortData.male,
            female: (state) => state.sortData.female,
            press: (state) => state.sortData.press,
            picture: (state) => state.sortData.picture,
        }),
    },
    methods: {
        changeItem(index) {
            this.sortList.forEach((el, idx) => {
                el.active = false;
                if (idx == index) {
                    el.active = true;
                }
            });
        },
        ...mapActions([types.INIT_SORT_DATA]),
        changeSort() {
            let key = "";
            this.sortList.forEach((el) => {
                if (el.active) {
                    key = el.target;
                }
            });
            return this[key];
        },
        currentTarget() {
            let target = "";
            this.sortList.forEach((el) => {
                if (el.active) {
                    target = el.target;
                }
            });
            return target;
        },
    },
    created() {
        this.$store.dispatch(types.INIT_SORT_DATA);
    },
};
</script>

<style lang="less" scoped>
.sort {
    display: flex;
    max-height: 82vh;
    overflow-y: scroll;
    margin: 10px 0 20px;
    .sort-list {
        flex: 2;
        border-right: 1px solid #f4f4f4;
        li {
            padding: 10px 0;
            span {
                display: block;
                height: 20px;
                line-height: 20px;
                letter-spacing: 2px;
                border-left: 3px solid transparent;
            }
            &.active span {
                border-left: 3px solid #e4939c;
            }
        }
    }
    .sort-content {
        flex: 10;
        max-height: 85vh;
        overflow-y: scroll;
        display: flex;
        flex-wrap: wrap;
    }
}
</style>
